import React, { useState, useEffect } from "react";
import {
  FaEnvelopeOpen,
  FaUser,
  FaCalendarTimes,
  FaMap,
  FaPhone,
  FaLock
} from "react-icons/fa";

const url = "https://randomuser.me/api/";
const defaultImage = "https://randomuser.me/api/portraits/men/75.jpg";

function App() {
  const [person, setPerson] = useState(null);
  const [title, setTitle] = useState("name");
  const [loading, setLoading] = useState(false);
  const [value, setValue] = useState("random person");

  const getData = async () => {
    setLoading(true);
    const response = await fetch(url);
    const data = await response.json();
    const user = data.results[0];

    const { phone, email } = user;
    const { first, last } = user.name;
    const {
      street: { number, name }
    } = user.location;
    const { password } = user.login;
    const {
      dob: { age }
    } = user;
    const { large: image } = user.picture;

    const newPerson = {
      phone,
      email,
      password,
      age,
      image,
      street: `${name} ${number}`,
      name: `${first} ${last}`
    };
    setPerson(newPerson);
    setLoading(false);
    setTitle("name");
    setValue(newPerson.name);
  };

  useEffect(() => {
    getData();
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    getData();
  };

  const handleData = (e) => {
    if (e.target.classList.contains("icon")) {
      let hoverData = e.target.dataset.label;
      setValue(person[hoverData]);
      setTitle(hoverData);
    }
  };

  return (
    <main>
      <div className="block bcg-black" />
      <div className="block">
        <div className="container">
          <img src={(person && person.image) || defaultImage} alt="logo" />
          <p className="user-title">My {title} is </p>
          <p className="user-value">{value}</p>
          <div className="values-list">
            <button
              onMouseOver={handleData}
              className="icon"
              data-label="email"
            >
              <FaEnvelopeOpen />
            </button>
            <button className="icon" data-label="name" onMouseOver={handleData}>
              <FaUser />
            </button>
            <button className="icon" data-label="age" onMouseOver={handleData}>
              <FaCalendarTimes />
            </button>
            <button
              className="icon"
              data-label="street"
              onMouseOver={handleData}
            >
              <FaMap />
            </button>
            <button
              className="icon"
              data-label="phone"
              onMouseOver={handleData}
            >
              <FaPhone />
            </button>
            <button
              className="icon"
              data-label="password"
              onMouseOver={handleData}
            >
              <FaLock />
            </button>
          </div>
          <button onClick={handleSubmit} type="submit" className="btn">
            {loading ? "Loading..." : "Random User"}
          </button>
        </div>
      </div>
    </main>
  );
}

export default App;
